import React, { useState, useEffect, useRef } from 'react';
import { BrowserRouter as Router, Route } from "react-router-dom";
import { Link } from "react-router-dom";
import axios from "axios";
import Search from './Search';
import CurrentMovie from './CurrentMovie';
import Dashbord from './Dashbord';

function Main() {
  const [isLoading, setIsLoading] = useState(false);
  const [movieType, setMovieType] = useState("");
  const [movieTitle, setMovieTitle] = useState("");
  const [movieYear, setMovieYear] = useState("");
  const [currentMovie, setCurrentMovie] = useState();
  const [selected, setSelected] = useState([]);
  const [nominated, setNominated] = useState(false)
  const [displaynumber, setdisplaynumber] = useState(0)
  const [dashbordNo, setDashbordNo] = useState(0);
  const [apiResponse, setApiResponse] = useState(false);
  const [nav, setNav] = useState(false);

  let key = "e9ee2678";

  const handleSubmit = (movieType,movieTitle,movieYear)=>{
   
    setMovieTitle(movieTitle);
    setMovieType(movieType);
    setMovieYear(movieYear);
    setApiResponse(true);

    const fetchMovies = async ()=> {
      setdisplaynumber(5)
      const res = await axios.get(`http://www.omdbapi.com/?apikey=${key}&t=${movieTitle}&y=${movieYear}&type=${movieType}`)

      if(res){
        setdisplaynumber(1)
      }else{
        setdisplaynumber(4)
      }
      setCurrentMovie(res.data);
    }
    fetchMovies();
  }

  useEffect(() => {
  }, [apiResponse])

  const handleNominated = ()=>{
    setSelected([...selected, selected])
    setNominated(true);
    setDashbordNo(1);
 
    console.log(selected);
  }

  const handleNav = ()=>{
    setNav(!nav)

  }
  const navToggle= nav?"display":null;

  return (
    <Router>
      <div className="Main">
        <div className="header">
          <h1>Masko Movie Award Nomination</h1>
          <p>Nominate your favourite movies </p>
        </div>
        <div className="container">
        <i className="fa fa-bars" onClick={handleNav}></i>
         <nav>
              <ul className={`nav navbar-nav ${navToggle}`}>
                <li>
                  <Link to="/">Home</Link>
                </li>
                <li>
                  <Link to="/dashbord">Dashbord</Link>
                </li>
              </ul>  
          </nav>
        
        <Route exact path="/" component={props=>(
            <React.Fragment>
            < Search 
            handleSubmit={handleSubmit} 
            />
            < CurrentMovie 
            currentMovie={currentMovie} 
            handleNominated={handleNominated}
            nominated={nominated}
            displaynumber={displaynumber}
            />
          </React.Fragment>
        )} />
        <Route path="/dashbord" component={(props)=>(
          <React.Fragment>
            < Dashbord
            selected={selected}
            dashbordNo={dashbordNo}
            />
          </React.Fragment>
        )}/>
        
       </div>
      </div>
    </Router>
  )
}

export default Main
