import React from 'react';
import unsplash1 from "../images/unsplash1.jpg";

function Dashbord({selected, dashbordNo}) {

  const topFiveSelected= selected.slice(0, 5);

  const handleRemove = (id)=>{
   // topFiveSelected.filter((e)=>{
    // return e.imdbID !== id
  //  })
  console.log(topFiveSelected);
  }

  if(dashbordNo === 0){
    return(
      <div className="card alert alert-warning">
        please go to the home page to nominate 5 movies
      </div>
    )
  }else if(dashbordNo === 1){
    return(
      <div className="moviecard">
    {
      topFiveSelected.map((movie)=>(
        <div>
          <img className="imgPoster" src={movie.Poster? movie.Poster:unsplash1} alt=" poster not available"/>
          <p>{movie? movie.Title:null}</p>
          {movie? movie.Year:null}
          <p>{movie? movie.Genre:null}</p>
          <p>{movie? movie.Plot:null}</p>
          <button 
          id={movie.imdbID}
          onClick={handleRemove}
          className="btn btn-block">remove from nomination
          </button>
        </div>
    ))
    }
  </div>
    )
  }else if(dashbordNo === 2){
    return(
      <div className="card alert alert-warning">
         page to nominate 5 movies
      </div>
    )
  }
}

export default Dashbord
